import React,{useState} from 'react'
import axios from 'axios'

function Post({message,id,setIsLoading,shared,targetName,isAdmin,onlySharedPosts,setOnlySharedPosts,posts,notSharedPosts,setNotSharedPosts}) {

    const [isShared,setIsShared] = useState(shared)
    const baseUrlOfApi = 'https://sspotted.herokuapp.com'
    //http://localhost:5000

    // DELETE HANDLER
    const deleteHandler = async(e) => {
        setIsLoading(true)
        const requestOptions = { method:'DELETE' }
        const res = await fetch(`${baseUrlOfApi}/posts/${id}`,requestOptions)
        const data = await res.json();
        if(data.ok === 1){
            const post = e.target.offsetParent.offsetParent;
            post.style.display ="none"
            if(shared){
                const newSharedPosts = onlySharedPosts.filter((post)=>post._id !== id)
                setOnlySharedPosts([...newSharedPosts])
            }else{
                const newNotSharedPosts = notSharedPosts.filter((post)=>post._id !== id)
                setNotSharedPosts([...newNotSharedPosts])
            }
        }else{
            console.log("oh ops");
        }
        setIsLoading(false)
        
    }

    // SHARE HANDLER "PATCH"
    const shareHandler = async (e) => {
        setIsLoading(true)
        await axios.patch(`${baseUrlOfApi}/posts/${id}`,{shared:!isShared})
        setIsShared(!isShared)
        const el = e.target
        const thisPost = posts.filter((post) => post._id === id)
        if(el.innerText === "Share") {
            el.innerText = "Shared"
            const post = e.target.offsetParent.offsetParent;
            post.style.display ="none"
            // setOnlySharedPosts([...onlySharedPosts,...thisPost])
            
            
        }else{
            el.innerText = "Share"
            // setOnlySharedPosts( posts.filter((post) => post._id !== id && post.shared === true))
            
        }
        if(shared){
            thisPost[0].shared = false
            const newSharedPosts = onlySharedPosts.filter((post)=> post._id !== id )
            setOnlySharedPosts([...newSharedPosts])
            setNotSharedPosts([...notSharedPosts,...thisPost])
        }else{
            thisPost[0].shared = true
            const newNotSharedPosts = notSharedPosts.filter((post)=>post._id !== id)
            setNotSharedPosts([...newNotSharedPosts])
            setOnlySharedPosts([...onlySharedPosts,...thisPost])
            
        }
        setIsLoading(false)
        
    }
    return (
        <div className="post" id={"a"+id}>

            <div className="postTextHolder">
                <span>for : {targetName}</span>
                <p className="postMessage" id="postMessage">
                    {message}
                </p>
            </div>
            {isAdmin?
                <div className="postOptions">
                    <button onClick={deleteHandler}>delete</button>
                    <button onClick={shareHandler}>{shared?"Shared":"Share"}</button>
                </div>
            :"" }
        </div>
    )
}

export default Post
