import React,{useState,useEffect,useRef} from 'react'
import { Link } from 'react-router-dom'
import axios from 'axios'

import Loading from './Loading'
import Post from './Post'
import NavBar from './NavBar'

import searchicon from '../images/search-icon.svg'
import addIcon from "../images/addicon.svg"


function UserPage() {

    var [posts,setPosts] = useState([])
    const hasFetchedApi = useRef(false)
    const [loading,setIsloading]= useState(false)
    const [userName,setUserName] = useState("")


    // Search Posts By Name Handler
    const searchPostsByNameHandler = async ()=> {
        setIsloading(true)

        if( document.getElementById("userNameInput").value === ""){
            window.location.reload(); 
            // setPosts([])
            // GetAllPosts()
            // setIsloading(false) 
            return null

        }else{
            
            const res = await axios.get(`https://sspotted.herokuapp.com/posts/relative/user/${userName}`)
            setPosts(res.data)
            document.getElementById('userNameInput').value = ""
        }
        setIsloading(false) 
    }

    // Get allPost On first loading
    useEffect(()=>{
        document.title="User Page"
        if(!hasFetchedApi.current){
            const GetAllPosts = async ()=>{
                setIsloading(true)
                const res = await fetch('https://sspotted.herokuapp.com/posts/shared-only')
                const data = await res.json()
                setPosts([...posts,...data])
                setIsloading(false)
            }
        
            GetAllPosts()
            hasFetchedApi.current = true
        }
    },[posts])

    // skew effect
    useEffect(()=>{
       const section = document.querySelector(".postsHolder");
        let currentPixel = window.pageYOffset
        //looper keeps running and keeps track of where the new pixel is
        const looper = function () {
            const newPixel = window.pageYOffset;
            const diff = newPixel - currentPixel
            const speed = diff * 0.35;
            
            section.style.transform = "skewY(" + speed + "deg)"
            
            currentPixel = newPixel;
            
            requestAnimationFrame(looper)
        }
        looper();
    },[])
    

    
    return (
        <>
            {loading?<Loading/>:""}
            <div className="UserPage">

                <NavBar setUserName={setUserName} searchPostsByNameHandler={searchPostsByNameHandler} searchicon={searchicon} logoutHandler={undefined} logoutImg={undefined} isAdmine={false}/>

                <div className="addMessageButtonHolder">
                    <Link to='/userPage/user-add-post' className="linkToUserAddMessage">
                        <button className="addMessageButton">
                            <img src={addIcon} alt="add"></img>
                            add message
                        </button>
                    </Link>
                </div>
                
                <div className="postsHolder">
                    {posts.map((post)=>(
                        <Post message={post.message} id={post._id} key={post._id}  setIsLoading={setIsloading} shared={post.shared} targetName={post.targetName} isAdmin={false}/>
                    ))}
                </div>

            </div>
        </>
    )
}

export default UserPage
