import React,{useEffect, useState,useRef} from 'react';
import {Link} from "react-router-dom";
import emailjs from 'emailjs-com';
import toast, { Toaster } from 'react-hot-toast';
import Recaptcha from 'react-google-invisible-recaptcha';
import { useMediaQuery } from 'react-responsive'

// IMPORTING IMAGES
import love from "../images/love.png";
import thinking from "../images/thinking.svg"
import safe from "../images/safe.svg"
import admin from "../images/admin.png"
import fast from "../images/fast.svg"
import happy from '../images/happy.png'
import social from "../images/social.png"

function Home() {
    // Onload Function
    useEffect(()=>document.title = "Home",[])
    // EmailJs to send the form to my email
    function sendEmail(e) {
        e.preventDefault();
        runRecaptcha()
        if(isHuman){
            emailjs.sendForm('service_61a7u38', 'template_1pd8d0s', e.target, 'user_Y97RCCHdu5oyJRsdXQuPo')
            .then(() => {
                notify()
            }, (error) => {
                console.log(error.text);
            });
            e.target.reset()
        } 
    }
    // notifications
    const notify = () => toast.success("Thanx For Your Feedback",{
        style:{
            fontFamily:"'Quicksand', sans-serif",
        }
    })
    // Recaptcha
    const refRecaptcha = useRef();
    const [isHuman,setIsHuman] = useState(false)

    const runRecaptcha = ()=>{
        refRecaptcha.current.execute()
    }
    const onResolved = () => {
        setIsHuman(true)
    }
    // responsive UI
    const isDeskTop = useMediaQuery({ query: '(min-width: 653px)' })

    
    return (
        <div className="HomePage">
            <Toaster/>
            
            <Recaptcha 
                ref={refRecaptcha} 
                sitekey="6LcS6yUcAAAAAI-lV3Tlzi-rZaiz-6PyAKSp0cyc" 
                onResolved={ onResolved } 
            />

            <div className="Cover">

                <h1 id="Logo"><span>SS</span>Potted</h1>
                <div className="scroll-down-holder">
                    <div className="chevron"></div>
                    <div className="chevron"></div>
                    <div className="chevron"></div>
                </div>

                <div className="TextHolder">
                    
                    <div className="innerTextHolder">

                        <p>
        
                            { isDeskTop && <span>Is it something you cannot confess ?<br/></span> }
                            Siliana spotted is the right place
                            { isDeskTop && <br/>}
                            to reveal the secret
                        
                        </p>
                        <Link to="/userPage">
                            <button id="CTA">try now</button>
                        </Link>
                        
                    </div>
                    <img src={love}alt="love" id="loveImg"/>
                </div>
            </div>
            
            <div className="WhatWeGive">

                <div className="textInnerWhatWeGiveHolder" id="textInnerWhatWeGiveHolder">
                    <h1>what's this !</h1>
                    <p>
                        . send a message for someone !
                        <br/> . tell someone about your feelings !
                        <br/> . see who have a crush on you !
                        <br/> . do that anonymously !
                    </p>
                </div>

                <div className="imageInnerWhatWeGiveHolder">
                    <img src={thinking} alt="thinking"/>
                </div>

            </div>

            <div className="services">
                <div className="cardInnerservices">
                    <img src={safe} alt="safe"/>
                    <div className="textInnerservicesHolder">
                        <h1>you are anonymous</h1>
                        <p>as you see no need to singup to add messages so you are aonymouse here !</p>
                    </div>
                </div>

                <div className="cardInnerservices">
                    <img src={admin} alt="admin"/>
                    <div className="textInnerservicesHolder">
                        <h1>only approved content</h1>
                        <p>when you submit a post you need to wait for the admin to approve it !</p>
                    </div>
                </div>

                <div className="cardInnerservices">
                    <img src={fast} alt="fast"/>
                    <div className="textInnerservicesHolder">
                        <h1>better UX UI design</h1>
                        <p>we made this app with react.js the best framework to build fast web apps </p>
                    </div>
                </div>
            </div>

            <div className="guide">
                <h1>how to use ?</h1>

                <div className="innerGuidecontent">
                    <div className="inerGuideTextHolder">
                        <h2>that's easy you know !</h2>
                        <ol>
                            <li>no need to make account just go to <Link to="userPage">user page</Link></li>
                            <li>you have the search bar so you can search for specifique posts</li>
                            <li>if you want to add a post just go to <Link to="userPage/user-add-post">user add posts</Link></li>
                            <li>to add a post you need to type for who the post and the message and click send </li>
                            <li>wait till the admin approve your post if it's SFW(safe for work)</li>
                        </ol>
                    </div>
                </div>

            </div>

            <div className="tryNow">

                <Link to="/userPage">
                    <h1>give it a try!</h1>
                </Link>
                <img src={happy} alt="happy"/>
                
            </div>

            <footer>
                
                <div className="innerFooter">
                    <form className="contactFormHolder" onSubmit={sendEmail}>
                        <h1>get in touch</h1>
                        <label>your name</label>
                        <input type="text" placeholder="type your name" name="userName" required/>
                        <label>message</label>
                        <textarea placeholder="type message" name="message" required/>
                        <button type="submit">submit</button>
                    </form>
                    <div className="socialMediaHolder">
                        <img src={social} alt="social"/>
                    </div>
                </div>
                
            </footer>
        </div>
    )
}

export default Home
