import React,{useState,useEffect,useRef} from 'react'
import { withRouter , Redirect } from 'react-router-dom'
import Post from './Post'
import Loading from "./Loading"
import NavBar from './NavBar'
import logoutImg from "../images/logout.svg"
import searchicon from '../images/search-icon.svg'
import axios from 'axios'
import { BrowserRouter as Router , Route , Switch,Link} from 'react-router-dom';


function AdminDashboard() {
    var [posts,setPosts] = useState([])
    const hasFetchedApi = useRef(false)
    const [loading,setIsloading]= useState(false)
    const [userName,setUserName] = useState("")
    const [goBackToLoginPage,setGoBackToLoginPage] = useState(false)
    
    // States for filtering
    const [onlySharedPosts,setOnlySharedPosts] = useState([])
    const [notSharedPosts,setNotSharedPosts] = useState([])
    
    
    // onload function
    useEffect(()=>{
        document.title = "Admin Dashboard"
        if(!hasFetchedApi.current){
        const GetAllPosts = async ()=>{
            const res = await fetch('https://sspotted.herokuapp.com/posts')
            const data = await res.json()
            setPosts([...posts,...data])
            const shared = data.filter((post) => post.shared === true)
            const notShared = data.filter((post) => post.shared === false)
            setOnlySharedPosts([...shared])
            setNotSharedPosts([...notShared])
            
            document.getElementById("rqtoshareFilter").click()
            
            
        }
        
        GetAllPosts()
        hasFetchedApi.current = true
        
    }
    },[posts])



    // Search Posts By Name Handler
    const searchPostsByNameHandler = async ()=> {
        setIsloading(true)

        if( document.getElementById("userNameInput").value === ""){
            window.location.reload(); 
            // setPosts([])
            // GetAllPosts()
            // setIsloading(false) 
            return null

        }else {
            const res = await axios.get(`https://sspotted.herokuapp.com/posts/relative/${userName}`)
            document.getElementById("userNameInput").value = ""
            setPosts(res.data.filter((post)=>post.shared === true))
            setPosts(res.data)
        }
        setIsloading(false) 
    }


    // Log out from admin dashboread handler
    const logoutHandler = async()=> {
        localStorage.setItem('isAuth',false);
        window.location.reload(); 
        return setGoBackToLoginPage(true)
    }



    // filters Handler
    const filterHandler = async(e) => {
        const el = document.getElementById("firstDivToSee")
        if(el) el.remove()
        const id = e.target.id
        const element = document.getElementById(id)
        const isInIt = element.classList.contains("filterClicked");
        if( isInIt ){
            return null
        }else {
            document.getElementById("sharedFilter").classList.toggle("filterClicked")
            document.getElementById("rqtoshareFilter").classList.toggle("filterClicked")
        }
        
        
    }

    // skew on scroll effect
    useEffect(()=>{
       const section = document.querySelector(".postsHolder");
        let currentPixel = window.pageYOffset
        //looper keeps running and keeps track of where the new pixel is
        const looper = function () {
            const newPixel = window.pageYOffset;
            const diff = newPixel - currentPixel
            const speed = diff * 0.35;
            
            section.style.transform = "skewY(" + speed + "deg)"
            
            currentPixel = newPixel;
            
            requestAnimationFrame(looper)
        }
        looper();
    },[])


    return (
        <>
            {loading?<Loading/>:""}
            {goBackToLoginPage?<Redirect to="/admin-login"/>:""}
            <Router>
                <div className="adminDashboard">
                    <NavBar setUserName={setUserName} searchPostsByNameHandler={searchPostsByNameHandler} searchicon={searchicon} logoutHandler={logoutHandler} logoutImg={logoutImg} isAdmine={true}/>
                    
                    <div className="filtersHolder">

                        <Link to="/admin-dashboard/notSharedPosts" className="filter filterClicked" id="rqtoshareFilter" onClick={filterHandler}>
                            request to share
                        </Link>

                        <Link to="/admin-dashboard/onlySharedPosts" className="filter" id="sharedFilter" onClick={filterHandler}>
                            shared
                        </Link>

                    </div>
                    
                    <div className="postsHolder">
                            
                            <Switch>
                                
                                <Route exact path="/admin-dashboard/notSharedPosts">
                                    {notSharedPosts.map((post)=>(
                                        <Post message={post.message} id={post._id} key={post._id}  setIsLoading={setIsloading} shared={post.shared} targetName={post.targetName} isAdmin={true} onlySharedPosts={onlySharedPosts} setOnlySharedPosts={setOnlySharedPosts} posts={posts} notSharedPosts={notSharedPosts} setNotSharedPosts={setNotSharedPosts}/>
                                    ))}   
                                </Route>
                
                                <Route exact path="/admin-dashboard/onlySharedPosts">
                                    {onlySharedPosts.map((post)=>(
                                        <Post message={post.message} id={post._id} key={post._id}  setIsLoading={setIsloading} shared={post.shared} targetName={post.targetName} isAdmin={true} onlySharedPosts={onlySharedPosts} setOnlySharedPosts={setOnlySharedPosts} posts={posts} notSharedPosts={notSharedPosts} setNotSharedPosts={setNotSharedPosts}/>
                                    ))}
                                </Route>
                            </Switch>
                        
                    </div>
                </div>
            </Router>
        </>
        
    )
}

export default withRouter(AdminDashboard)
