import React,{useState,useEffect} from 'react'
import adminImg from '../images/admin.svg'
import { useHistory } from 'react-router'
import { Redirect } from 'react-router-dom'

function AdminLogin({isAuth,setIsAuth}) {
    // adding title to page
    useEffect(()=>document.title = "Admin Login",[])

    const history = useHistory()
    const [adminName,setAdminName] = useState("")
    const [password,setPassword] = useState("")
    const onChangeHandler = (e) =>{
        const nameOfTarget = e.target.getAttribute('name');
        if(nameOfTarget === 'adminName'){
            setAdminName(e.target.value)
        }else{
            setPassword(e.target.value)
        }
    }
    const onClickHandler = ()=>{

        if( (adminName === "koussay" || adminName === "Koussay")&& password === process.env.REACT_APP_ADMIN_PASSWORD)
        {   
            // Here He Logged In Successfully
            document.querySelector("#adminName").style.border = "none"
            document.querySelector("#password").style.border = "none"
            localStorage.setItem("isAuth",true)
            setIsAuth(true)
            history.push("/admin-dashboard")

        }else{
            document.querySelector("#adminName").style.border = "none"
            document.querySelector("#password").style.border = "none"
            if( adminName !== "koussay" && adminName !== "Koussay"){
                const target = document.querySelector("#adminName")
                target.value = ""
                target.style.border = "1px solid #ff0f0f"

            }else{
                const target = document.querySelector("#password")
                target.value = ""
                target.style.border = "1px solid #ff0f0f "
            }
        }
    }
    if(isAuth === "true")
    {
    return <Redirect to="/admin-dashboard"/>
    }
    else
    {
        return (
            <div className="AdminPage">

                <div className="innerAdminPageHolder">

                    <div className="innerAdminPageImgHolder">
                        <img src={adminImg} alt="admin"/>
                    </div>

                    <div className="innerAdminPageFormHolder">
                        <div className="form">
                            <span>Name</span>
                            <input type="text" placeholder="type admin name" name="adminName" onChange={onChangeHandler} id="adminName"/>
                            <span>Password</span>
                            <input type="password" placeholder="type password" name="password" onChange={onChangeHandler} id="password"/>
                            <button onClick={onClickHandler}>login</button>
                        </div>
                    </div>

                </div>

            </div>
        )
    }
}

export default AdminLogin
