import React from 'react'

function NavBar({setUserName,searchPostsByNameHandler,searchicon,logoutHandler,logoutImg,isAdmine}) {

    
    const listener = event => {

        if (event.code === "Enter" || event.code === "NumpadEnter") {
            event.preventDefault();
            searchPostsByNameHandler()
        }
    }
    
    return (
        <nav>
            <div className="innerNav">
                <h1 className="Logo"><span>SS</span>Potted</h1>
                <div className="searchBox">
                    <input type="text" placeholder="type person name" onChange={(e)=>setUserName(e.target.value)} id="userNameInput" onKeyDown={listener}/>
                    <button onClick={searchPostsByNameHandler}><img src={searchicon} alt="search"/></button>
                </div>
                {isAdmine?
                    <button className="Logout" onClick={logoutHandler}><img src={logoutImg} alt="logout"/></button>
                :""}    
            </div>
        </nav>
    )
}

export default NavBar
