import React,{useState,useEffect,useRef} from 'react';

import toast, { Toaster } from 'react-hot-toast';


import Recaptcha from 'react-google-invisible-recaptcha';

import Loading from './Loading';

import Done from './Done';

import Popup from './Popup';

import axios from 'axios';

import warningImg from '../images/warning.svg'

// notifications
const notify = () => toast.error("Input Fields Must Not Be Empty",{
    style:{
        fontFamily:"'Quicksand', sans-serif"
    }
})

function User() {
    // chnaging page title
    useEffect(()=>document.title = "Send Message",[])
    
    const [message,setMessage] = useState("")
    const [target,setTarget] = useState("")
    const [loading,setLoding] = useState(false)
    const [showDone,setShowDone] = useState(false)
    const [showPopup,setShowPopup] =useState(false)
    const [counter,setCounter] = useState(0)

    // Recaptcha
    const refRecaptcha = useRef();
    const [isHuman,setIsHuman] = useState(false)

    const runRecaptcha = ()=>{
        setCounter(counter+1)
        refRecaptcha.current.execute()
    }
    const onResolved = () => {
        setIsHuman(true)
    }
    
    const baseUrlOfApi = 'https://sspotted.herokuapp.com'
    //http://localhost:5000

    // onChange Handler
    const onChnageHandler = (e)=>{
        setMessage(e.target.value)
    }
    const onChnageHandlerForTargetName = (e) =>{
        setTarget(e.target.value)
    }
    const [isFirtTime,setIsFirtTime] = useState(true)
    const onPostHandler = async(e) => {
        console.log(isFirtTime);
        console.log(isHuman);
        if(isFirtTime) return setIsFirtTime(false)
        if(message === "" || target === ""){
            setShowPopup(false)
            notify()
            return null
        }
        setLoding(true)
        if(isHuman){
            const res = await axios.post(`${baseUrlOfApi}/posts`,{message:message,target:target});
            if(res){
                document.getElementById("message").value = ""
                document.getElementById("personeTarget").value = ""
                setLoding(false)
                setShowDone(true)
                setMessage("")
                setTarget("")
                setInterval(()=>{
                    setShowDone(false)
                    
                },3500)
            }else{
                alert("sorry ther's a porbleme");
            }    
        }else{
            console.log("robot detected");
        }
        setLoding(false)

    }
    useEffect(()=>{
        onPostHandler()
        console.log("im in effect");
        /* eslint-disable-next-line */
    },[isHuman,counter])
    return (
        <>
            <div className="userPageCover"/>
            {loading ? <Loading/>:""}
            {showDone ? <Done/>:""}
            {showPopup ? <Popup PopupText="message or for who this message can't be empty" PopupImg={warningImg} PopupImgAlt="warning" setShowPopup={setShowPopup}/>:""}
            <Toaster />
            <Recaptcha 
                ref={refRecaptcha} 
                sitekey="6LcS6yUcAAAAAI-lV3Tlzi-rZaiz-6PyAKSp0cyc" 
                onResolved={ onResolved } 
            />
            <div className="UserPageAddMessage">
                <h1>write your message</h1>
                <input placeholder="type for who this message" type="text" required id="personeTarget" onChange={onChnageHandlerForTargetName}/>
                <textarea placeholder="type the message" onChange={onChnageHandler} required id="message"/>
                <button onClick={runRecaptcha}  className="postButton">send</button>
            </div>
        </>
        
    )
}

export default User
