import React,{useState,useEffect} from 'react';
import { BrowserRouter as Router ,Route,Switch } from 'react-router-dom';
import AdminLogin from './components/AdminLogin';
import Home from './components/Home';
import User from './components/User';
import UserPage from './components/UserPage';
import AdminDashboard from './components/AdminDashboard';
import ProtectedRoute from './components/ProtectedRoute';

function App() {

  const [isAuth,setIsAuth] = useState(false)

  useEffect(()=>{

    const isAuthInlocalStorage = localStorage.getItem("isAuth")
    
    if(isAuthInlocalStorage === "null"){

      localStorage.setItem("isAuth",false)
      setIsAuth(false)

    } else{
      setIsAuth(isAuthInlocalStorage)
    }

  },[])

  return (
    <Router>
      <Switch>
        <Route exact path="/" component={Home}/>
        <Route  exact path="/userPage" component={UserPage}/>
        <Route  path="/userPage/user-add-post" component={User}/>
        <Route path="/admin-login" component={()=><AdminLogin isAuth={isAuth} setIsAuth={setIsAuth}/>}/>
        <ProtectedRoute path="/admin-dashboard" Component={AdminDashboard}/>
      </Switch>
    </Router>
  );
}

export default App;
