import React from 'react'

function Popup({PopupText,PopupImg,PopupImgAlt,setShowPopup}) {
    return (
        <div className="Popup">
            
            <div className="popupCard">

                <div className="closingHolder">
                    <button onClick={()=>setShowPopup(false)}>
                        X
                    </button>
                </div>

                <img src={PopupImg} alt={PopupImgAlt}/>

                <p id="popUpText">
                    {PopupText}
                </p>

            </div>
        </div>
    )
}

export default Popup
